.about_container {
  grid-template-columns: 2fr 3fr;
  column-gap: 3rem;
  align-items: center;
  min-height: 60vh;
  justify-items: center;
}
.about_img {
  width: 100%;
  max-width: 400px;
  aspect-ratio: 3/4;
  object-fit: cover;
  border-radius: var(--radius-2xl);
  justify-self: center;
  opacity: 0;
  transform: scale(0.8);
  transition: all 0.6s ease-in-out;
  box-shadow: var(--shadow-xl);
  position: relative;
  overflow: hidden;
}

.about_img.loaded {
  opacity: 1;
  transform: scale(1);
}

.about_img::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg, rgba(102, 126, 234, 0.1) 0%, rgba(118, 75, 162, 0.1) 100%);
  opacity: 0;
  transition: opacity 0.3s ease;
}

.about_img:hover::after {
  opacity: 1;
}
.about_info {
  display: grid;
  grid-template-columns: repeat(2,1fr);
  gap: 1rem;
  margin-bottom: var(--mb-2);
  width: 100%;
  max-width: 480px;
  margin: 0 auto var(--mb-2);
}
.about_box {
  background: var(--container-color);
  border: 1px solid var(--border-color);
  border-radius: var(--radius-xl);
  text-align: center;
  padding: 1.5rem 1.25rem;
  box-shadow: var(--shadow-sm);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  backdrop-filter: var(--backdrop-blur);
  position: relative;
  overflow: hidden;
}

.about_box::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: var(--primary-gradient);
  transform: scaleX(0);
  transition: transform 0.3s ease;
}

.about_box:hover {
  transform: translateY(-4px);
  box-shadow: var(--shadow-lg);
  border-color: transparent;
}

.about_box:hover::before {
  transform: scaleX(1);
}
.about_icon {
  font-size: 1.5rem;
  background: var(--primary-gradient);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  margin-bottom: var(--mb-0-5);
  transition: all 0.3s ease;
}

.about_box:hover .about_icon {
  transform: scale(1.1) rotate(5deg);
}
.about_title {
  font-size: var(--small-font-size);
  font-weight: var(--font-medium);
}
.about_subtitle {
  font-size: var(--tiny-font-size);
}
.about_description {
  padding: 0;
  margin-bottom: var(--mb-2-5);
  line-height: 1.7;
  width: 100%;
  max-width: 480px;
  margin: 0 auto var(--mb-2-5);
}

.about_data {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
}

/*=============== BREAKPOINTS ===============*/
/* For large devices (태블릿) */
@media screen and (max-width: 992px) {
  .about_container {
    grid-template-columns: 1fr;
    row-gap: 2.5rem;
    text-align: center;
    padding: 0 1rem;
  }
  .about_img {
    max-width: 350px;
    width: 100%;
    justify-self: center;
  }
  .about_box {
    padding: 1rem 0.75rem;
  }
  .about_data {
    text-align: center;
    max-width: 100%;
    margin: 0 auto;
  }
  .about_info {
    max-width: 460px;
    gap: 1.2rem;
  }
  .about_description {
    margin: 0 auto var(--mb-2-5);
    max-width: 460px;
  }
}

/* For tablet devices (작은 태블릿) */
@media screen and (max-width: 768px) {
  .about_img {
    max-width: 320px;
  }
  .about_info {
    max-width: 400px;
    gap: 1rem;
  }
  .about_box {
    padding: 0.875rem 0.625rem;
  }
  .about_description {
    max-width: 400px;
  }
}

/* For medium devices */
@media screen and (max-width: 576px) {
  .about_container {
    padding: 0 0.5rem;
  }
  .about_img {
    max-width: 280px;
  }
  .about_info {
    max-width: 340px;
    gap: 0.875rem;
  }
  .about_box {
    padding: 0.75rem 0.5rem;
  }
  .about_description {
    max-width: 340px;
  }
}

/* For small devices */
@media screen and (max-width: 350px) {
  .about_container {
    padding: 0 0.25rem;
  }
  .about_img {
    max-width: 240px;
  }
  .about_info {
    max-width: 280px;
    gap: 0.75rem;
  }
  .about_box {
    padding: 0.625rem 0.375rem;
    font-size: 0.875rem;
  }
  .about_description {
    padding: 0 0.5rem;
    max-width: 280px;
  }
}

/* For extra small devices */
@media screen and (max-width: 320px) {
  .about_container {
    padding: 0;
  }
  .about_img {
    max-width: 220px;
  }
  .about_info {
    max-width: 260px;
    gap: 0.625rem;
  }
  .about_box {
    padding: 0.5rem 0.25rem;
    font-size: 0.8rem;
  }
  .about_icon {
    font-size: 1.25rem;
  }
  .about_title {
    font-size: calc(var(--tiny-font-size) * var(--font-scale, 1));
  }
  .about_subtitle {
    font-size: calc(var(--tiny-font-size) * 0.85 * var(--font-scale, 1));
  }
  .about_description {
    padding: 0 0.25rem;
    font-size: calc(var(--small-font-size) * 0.9 * var(--font-scale, 1));
    max-width: 260px;
  }
}
